<template>
  <div id="app">
    <header class="header">
      <!-- logo -->
      <router-link class="logo-link" to="/">
        <img src="https://aicv-1307107697.cos.ap-guangzhou.myqcloud.com/asserts/icon/logo1.png" alt="logo" class="header-logo">
        <span class="app-title">AI</span>
      </router-link>

      <!--  -->
      <div class="nav-right">
        <!--  -->
        <div v-if="isLoggedIn" 
             class="user-menu-container"
             @mouseenter="showMenu = true"
             @mouseleave="showMenu = false">
          <div class="username">{{ username }}</div>
          <transition name="fade">
            <ul v-show="showMenu" class="dropdown-menu">
              <li @click="handleLogout"></li>
            </ul>
          </transition>
        </div>

        <!--  -->
        <template v-else>
          <span class="nav-item"></span>
          <span class="nav-item"></span>
          <span class="nav-item"></span>
          <router-link to="/auth" class="user-icon-link">
            <img src="https://aicv-1307107697.cos.ap-guangzhou.myqcloud.com/asserts/icon/user-icon.svg" alt="" class="user-icon">
          </router-link>
          <button class="free-trial"></button>
        </template>
      </div>
    </header>

    <router-view class="router-view" />
  </div>
</template>

<script>
import AuthService from '@/utils/auth'
import { useToast } from 'vue-toastification'

export default {
  name: 'App',
  data() {
    return {
      showMenu: false
    }
  },
  setup() {
    const toast = useToast()
    return { toast }
  },
  computed: {
    isLoggedIn() {
      return AuthService.isLoggedIn()
    },
    username() {
      return AuthService.getUserContact()
    }
  },
  methods: {
    handleLogout() {
      AuthService.logout()
      this.toast.success('')
      this.showMenu = false
      this.$router.push('/')
    }
  }
}
</script>

